import img_bg from "../../assets/img/bg2.png";
import NavBar from "../../Components/Nav/NavBar";
import FlipCardList from "../../Components/Cards/FlipCardList";
import { CSSProperties, useEffect } from "react";
import SwitchCardList from "../../Components/Cards/SwitchCardList";
import event01 from "../../assets/img/event01.jpg";
import event02 from "../../assets/img/event02.jpg";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import { Autoplay } from "swiper/modules";
import goals from "../../assets/img/goals/4.png";

import AOS from "aos";
import "aos/dist/aos.css";
export default function LandingPage() {
  const heroStyle: CSSProperties = {
    backgroundImage: `url(${img_bg})`,
    backgroundSize: "cover",
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
    backgroundAttachment: "fixed",
  };

  useEffect(() => {
    AOS.init();
  }, []);

  return (
    <div>
      <NavBar />
      <div className="h-[100svh]" style={heroStyle}>
        <div className="flex flex-col justify-end pb-[18rem] text-white h-full container mx-auto">
          <h1 className="text-6xl font-bold tracking-[8px] opacity-80">
            永續至上
          </h1>
          <p className="text-xl font-light mt-8 opacity-80">
            致力環境保護發展，守護每位員工權益， 承諾給予社會正面價值。
          </p>
        </div>
      </div>
      <div className="bg-white -mt-[10rem] rounded-t-[5rem]">
        <div className="h-[50px] w-full bg-[#FF8D50] rounded-full container mx-auto -translate-y-1/2 flex items-center px-8 text-white">
          最新消息
        </div>
        <div className="container mx-auto py-24">
          <p
            className="text-center font-semibold  text-5xl  text-[#555555] tracking-[6px] "
            data-aos="fade-up"
          >
            相信深耕 共展未來
          </p>
          <p
            className="text-center text-[#555555] mt-8 font-light"
            data-aos="fade-up"
          >
            {" "}
            致力實現經濟、社會和環境之間的平衡，承諾在創造價值的同時兼顧永續發展，為員工、合作夥伴、社區帶來正面的影響。
          </p>
          <FlipCardList />
        </div>
        <div
          className="bg-[#ED6B2C] "
          data-aos="fade-up"
          data-aos-duration="1000"
        >
          <div className="bg-[#ED6B2C] w-full h-[100px]"></div>
          <div className="container mx-auto">
            <SwitchCardList />
          </div>
        </div>

        <div className="container mx-auto xl:px-36">
          <p
            className="text-center font-semibold  text-5xl  text-[#555555] tracking-[6px] mt-[200px] "
            data-aos="fade-up"
          >
            熱血至上 公益你我
          </p>
          <Swiper
            spaceBetween={50}
            slidesPerView={1}
            autoplay={{
              delay: 4500,
              disableOnInteraction: false,
            }}
            modules={[Autoplay]}
            className="flex flex-col justify-center"
          >
            <SwiperSlide>
              {" "}
              <div className="grid grid-cols-3 mt-24">
                <div className="col-span-2 relative">
                  <img
                    src={event01}
                    alt="event01"
                    className="aspect-[25/12] object-cover rounded-[3rem]"
                    data-aos="zoom-in"
                  />
                  <div
                    className="absolute -right-[10%] -bottom-[5%] bg-[#D9F2F3] p-8 rounded-[1rem]"
                    data-aos="zoom-in"
                    data-aos-delay="100"
                  >
                    <p className="font-bold text-xl  tracking-[2px]">
                      至上電子 × <br />
                      國立臺灣師範大學
                    </p>
                    <p className="text-sm mt-2  tracking-[2px]">
                      動手做STEAM多元智能晨光時間
                    </p>
                  </div>
                </div>
                <div
                  className="col-span-3 mt-16"
                  data-aos="fade-in"
                  data-aos-delay="200"
                >
                  <p className="text-[#ED6B2C] font-semibold text-[32px] tracking-[2px]">
                    教育扎根在長安國中
                  </p>
                  <p className="mt-6 text-[20px] font-light tracking-[2px] leading-[40px]">
                    本公司與國立臺灣師範大學科技應用與人力資源發展學系攜手合作，展開「動手做STEAM多元智能晨光時間」！活動旨在啟發長安國中特教學生發展多元智能，長安國中校方對計畫的執行給予高度評價，感謝至上電子和臺師大的合作和支持，期待未來繼續合作。透過贊助為有不同學習需求的孩子們搭建通往實現夢想的橋樑，培養其興趣和熱情。
                  </p>

                  <div className="text-center mt-8">
                    <button className="text-[20px] border-[1px] rounded-full border-[#ED6B2C] text-[#ED6B2C] py-2 px-6 duration-300 hover:text-white hover:bg-[#ED6B2C] hover:scale-[1.1]">
                      查看更多
                    </button>
                  </div>
                </div>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              {" "}
              <div className="grid grid-cols-3 mt-24">
                <div className="col-span-2 relative">
                  <img
                    src={event02}
                    alt="event01"
                    className="aspect-[25/12] object-cover rounded-[3rem]"
                    data-aos="zoom-in"
                  />
                  <div
                    className="absolute -right-[10%] -bottom-[5%] bg-[#D9F2F3] p-8 rounded-[1rem]"
                    data-aos="zoom-in"
                    data-aos-delay="100"
                  >
                    <p className="font-bold text-xl  tracking-[2px]">
                      至上電子 × <br />
                      台北捐血中心
                    </p>
                    <p className="text-sm mt-2  tracking-[2px]">愛心捐血活動</p>
                  </div>
                </div>
                <div
                  className="col-span-3 mt-16"
                  data-aos="fade-in"
                  data-aos-delay="200"
                >
                  <p className="text-[#ED6B2C] font-semibold text-[32px] tracking-[2px]">
                    熱心捐血
                  </p>
                  <p className="mt-6 text-[20px] font-light tracking-[2px] leading-[40px]">
                    2023年10月同仁參與捐血志工，協助辦理「拱天慈善功德會」於2023年10月在南港玉成公園福德宮前舉辦的愛心捐血活動。
                    <br />
                    2023年12月與「台北捐血中心」攜手舉辦愛心捐血活動，號召公司同仁及社區民眾一起參與，透過實際的行動展現愛心，為社會盡一份心力。
                  </p>

                  <div className="text-center mt-8 pb-12">
                    <button className="text-[20px] border-[1px] rounded-full border-[#ED6B2C] text-[#ED6B2C] py-2 px-6 duration-300 hover:text-white hover:bg-[#ED6B2C] hover:scale-[1.1]">
                      查看更多
                    </button>
                  </div>
                </div>
              </div>
            </SwiperSlide>
          </Swiper>
        </div>

        <div className="bg-[#FF8D50] bg-opacity-[5%] pb-24">
          <div className="container xl:px-[10rem] mx-auto">
            <p className="text-center font-semibold  text-5xl  text-[#555555] tracking-[6px] mt-12 pt-24 pb-12">
              特別報導
            </p>
            <Swiper
              spaceBetween={50}
              slidesPerView={1}
              modules={[Autoplay]}
              className="flex flex-col justify-center"
            >
              <SwiperSlide>
                <div className="grid grid-cols-2 gap-x-36 gap-y-6">
                  <div className="relative">
                    <img
                      src={goals}
                      alt="template"
                      className="aspect-square rounded-[2rem] object-cover translate-y-[4rem]"
                      data-aos="zoom-in"  data-aos-duration="500"
                    />
                  </div>
                  <div>
                    <p className="text-[#FF8D50] font-semibold text-[32px] pt-[4rem]">
                      新能源部門投資發展
                    </p>
                    <p className="mt-12 text-[20px] font-light tracking-[2px] leading-[40px]"  data-aos="fade-in"  data-aos-duration="800" data-aos-delay="500">
                      本公司成立電機應用研發部門，延攬專業電子、電機、軟韌體相關人才，加大力度投資低壓電力驅動系統自主研發。目前已成功完成階段性目標，實現單體電機性能相關數據驗證且超過預期效能。
                    </p>
                    <button className="mt-12 text-[20px] border-[1px] rounded-full border-[#ED6B2C] text-[#ED6B2C] py-2 px-6 duration-300 hover:text-white hover:bg-[#ED6B2C] hover:scale-[1.1]" data-aos="zoom-in"  data-aos-duration="800" data-aos-delay="1000">
                      查看更多
                    </button>
                  </div>
                  <div>
                    <p className="text-[#FF8D50] font-semibold text-[32px] pt-[6rem]">
                      2020《天下》2000大調查 — 榮獲最強50大集團殊榮
                    </p>
                    <p className="mt-12 text-[20px] font-light tracking-[2px] leading-[40px]"  data-aos="fade-in"  data-aos-duration="800" data-aos-delay="500">
                      臺灣最強50大集團，20年誰還在榜上？誰又躍起擠進榜中？現今除了面對貿易戰、肺炎的挑戰，提出短期方案面對變局，也要擘劃長期結構轉型、人才教育佈局。
                    </p>
                    <button className="mt-12 text-[20px] border-[1px] rounded-full border-[#ED6B2C] text-[#ED6B2C] py-2 px-6 duration-300 hover:text-white hover:bg-[#ED6B2C] hover:scale-[1.1]"  data-aos="zoom-in"  data-aos-duration="800" data-aos-delay="1000">
                      查看更多
                    </button>
                  </div>
                  <div className="relative">
                    <img
                      src={goals}
                      alt="template"
                      className="aspect-square rounded-[2rem] object-cover  absolute bottom-0"
                      data-aos="zoom-in"  data-aos-duration="500"
                    />
                  </div>
                </div>
              </SwiperSlide>{" "}
              <SwiperSlide>
                <div className="grid grid-cols-2 gap-x-36 gap-y-6">
                  <div className="relative">
                    <img
                      src={goals}
                      alt="template"
                      className="aspect-square rounded-[2rem] object-cover translate-y-[4rem]"
                      data-aos="zoom-in"  data-aos-duration="500"
                    />
                  </div>
                  <div>
                    <p className="text-[#FF8D50] font-semibold text-[32px] pt-[4rem]">
                      自行研發科技醫療器材
                    </p>
                    <p className="mt-12 text-[20px] font-light tracking-[2px] leading-[40px]"  data-aos="fade-in"  data-aos-duration="800" data-aos-delay="500">
                      本公司成功研發出心音貼片集體外快篩檢測系統，不僅與特定醫院配合使用，體外快篩檢測系統還獲得中華民國發明專利與中國新型專利。
                    </p>
                    <button className="mt-12 text-[20px] border-[1px] rounded-full border-[#ED6B2C] text-[#ED6B2C] py-2 px-6 duration-300 hover:text-white hover:bg-[#ED6B2C] hover:scale-[1.1]"  data-aos="zoom-in"  data-aos-duration="800" data-aos-delay="1000">
                      查看更多
                    </button>
                  </div>
                  <div>
                    <p className="text-[#FF8D50] font-semibold text-[32px] pt-[6rem]">
                      代理生技品牌，跨足再生醫學領域
                    </p>
                    <p className="mt-12 text-[20px] font-light tracking-[2px] leading-[40px]"  data-aos="fade-in"  data-aos-duration="800" data-aos-delay="500">
                      本公司於 2016 年投資成立子公司保康特生物科技股份有限公司 (
                      以下簡稱：保康特生技 )，主要業務為自
                      行研發快篩體外檢測系統、心音貼片等，以及代理多國新創生技品牌產品，秉持多角化經營和以人為本的醫療
                      科技及服務理念，旨在提供人類更好的生活與未來。
                    </p>
                    <button className="mt-12 text-[20px] border-[1px] rounded-full border-[#ED6B2C] text-[#ED6B2C] py-2 px-6 duration-300 hover:text-white hover:bg-[#ED6B2C] hover:scale-[1.1]"  data-aos="zoom-in"  data-aos-duration="800" data-aos-delay="1000">
                      查看更多
                    </button>
                  </div>
                  <div className="relative">
                    <img
                      src={goals}
                      alt="template"
                      className="aspect-square rounded-[2rem] object-cover absolute bottom-0"
                      data-aos="zoom-in"  data-aos-duration="500"
                    />
                  </div>
                </div>
              </SwiperSlide>
            </Swiper>
          </div>
        </div>

        <div className="container mx-auto pb-24">
          <p className="text-center font-semibold  text-5xl  text-[#555555] tracking-[6px] pt-24  pb-12">
            報告書下載
          </p>
          <div className="grid grid-cols-2 gap-8">
            <div className="w-full  rounded-[2rem] px-12 py-10 text-[24px] duration-300 hover:scale-[1.05] hover:shadow-xl cursor-pointer bg-[#ED6B2C] text-white">
              2023年永續報告書
            </div>
            <div className="w-full bg-[#F5F5F5] rounded-[2rem] px-12 py-10 text-[24px] duration-300 hover:scale-[1.05] hover:shadow-xl cursor-pointer">
              2022年永續報告書
            </div>
            <div className="w-full bg-[#F5F5F5] rounded-[2rem] px-12 py-10 text-[24px] duration-300 hover:scale-[1.05] hover:shadow-xl cursor-pointer">
              2021年永續報告書
            </div>
            <div className="w-full bg-[#F5F5F5] rounded-[2rem] px-12 py-10 text-[24px] duration-300 hover:scale-[1.05] hover:shadow-xl cursor-pointer">
              2020年永續報告書
            </div>
          </div>
        </div>

        <div className="bg-[#D9F2F3] mx-auto mx-4 rounded-[2rem] h-[500px]"></div>
      </div>
    </div>
  );
}
